// Component-specific styles that cannot be replaced with Tailwind
// Status-based record styling (complex selectors)
.compact-record {
  &.both-environments {
    border-left: 4px solid #28a745;
  }

  &.cypherView-only {
    border-left: 4px solid #007bff;
  }

  &.key-only {
    border-left: 4px solid #ff9800;
  }
}

// Record type badges (complex conditional styling)
.record-type-badge {
  &.badge-telemetry {
    background: rgba(0, 123, 255, 0.15);
    color: #004085;
    border: 1px solid rgba(0, 123, 255, 0.3);
  }

  &.badge-event {
    background: rgba(255, 193, 7, 0.15);
    color: #856404;
    border: 1px solid rgba(255, 193, 7, 0.3);
  }

  &.badge-trip {
    background: rgba(40, 167, 69, 0.15);
    color: #155724;
    border: 1px solid rgba(40, 167, 69, 0.3);
  }

  &.badge-unknown {
    background: rgba(108, 117, 125, 0.15);
    color: #495057;
    border: 1px solid rgba(108, 117, 125, 0.3);
  }
}

// Field value styling with conditional backgrounds
.cv-value {
  background: rgba(0, 123, 255, 0.08);
  border-left: 2px solid #007bff;
  color: #004085;

  &.has-value {
    font-weight: 500;
  }
}

.key-value {
  background: rgba(255, 193, 7, 0.15);
  border-left: 2px solid #ffc107;
  color: #856404;

  &.has-value {
    font-weight: 500;
  }
}

// Field row status-based styling (complex conditional styling)
.field-row {
  &.identical {
    .cv-value,
    .key-value {
      color: #155724;
      font-weight: 500;
    }
  }

  &.different {
    .cv-value,
    .key-value {
      color: #721c24;
      font-weight: 600;
      background-color: rgba(220, 53, 69, 0.1);
    }
  }

  &.missing_test .cv-value {
    color: #721c24;
    font-weight: 600;
    background: rgba(220, 53, 69, 0.15);
    border-left-color: #dc3545;
  }

  &.missing_prod .key-value {
    color: #721c24;
    font-weight: 600;
    background: rgba(220, 53, 69, 0.15);
    border-left-color: #dc3545;
  }
}

// Compact card styling
.compact-card {
  :ng-deep .p-card-header {
    padding: 0.75rem 1rem;
    font-size: 0.875rem;
    font-weight: 600;
  }

  :ng-deep .p-card-content {
    padding: 0.75rem 1rem;
  }
}

// PrimeNG component customizations
:ng-deep .filter-btn {
  .p-button-label {
    font-size: 0.75rem;
    font-weight: 600;
  }
}

// Compact input styling for PrimeNG components
:ng-deep .p-autocomplete,
:ng-deep .p-datepicker,
:ng-deep .p-select,
:ng-deep .p-inputtext {
  .p-inputtext {
    padding: 0.5rem 0.75rem;
    font-size: 0.875rem;
    height: auto;
    min-height: 2.25rem;
  }
}

:ng-deep .p-autocomplete-input,
:ng-deep .p-datepicker-input {
  padding: 0.5rem 0.75rem !important;
  font-size: 0.875rem !important;
  height: 2.25rem !important;
}

:ng-deep .p-select {
  min-height: 2.25rem;

  .p-select-label {
    padding: 0.5rem 0.75rem;
    font-size: 0.875rem;
  }
}

:ng-deep .p-button {
  padding: 0.5rem 1rem;
  font-size: 0.875rem;

  &.p-button-sm {
    padding: 0.375rem 0.75rem;
    font-size: 0.75rem;
  }
}
